@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigation--container {
  width: 367px;
  height: 351px;
  margin-top: 250px;
  margin-left: 5%;
  background-color: #ebedfc;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navigationItem--align-text {
  cursor: pointer;
  width: 80%;
  padding: 15px 0px;
  margin: 0px;
  transition: 0.3s ease;
  font-family: "Poppins", sans-serif;
  display: inline-flex;
  align-items: center;
  padding-left: 25px;
  margin-right: 5px;
}

.navigationItem--align-text:hover {
  background-color: #ffffff;
  border-radius: 7px;
}

.navitionItem--margin-right-icon {
  margin-left: 13px;
}

.header--title {
  font-family: "Poppins", sans-serif;
  color: #262832;
  margin-bottom: 35px;
}

.faq--container {
  width: 600px;
  height: 475px;
  background-color: #f5f6fe;
  margin-left: 50px;
  margin-top: 225px;
}

.accordion--title {
  display: flex;
  justify-content: space-between;
  padding: 13px 0px;
  cursor: pointer;
  margin-top: 10px;
  font-weight: 500;
  color: #262832;
}

.accordion--content {
  margin-top: 10px;
  margin-left: 38px;
  font-weight: 500;
  font-size: 14px;
  color: #1f2029;
  word-spacing: 1px;
}

.accordions--container {
  font-family: "Poppins", sans-serif;
}

.page--container {
  width: 90%;
  display: flex;
  justify-content: space-around;
  margin: auto;
}

